import {IconStyle} from '@here/hds-icon';
import {IconCategory} from '@here/hds-iconlibrary';
import * as React from 'react';
import {Avatar, AvatarSize} from '@here/hds-avatar';

// eslint-disable-next-line no-new
new Avatar();

interface HDSAvatarProperties extends React.HTMLAttributes<HTMLElement> {
  disabled?: boolean;
  borderless?: boolean;
  size?: AvatarSize;
  name?: string;
  imageUrl?: string;
  icon?: string;
  iconCategory?: IconCategory;
  iconStyle?: IconStyle;
}

const HDSAvatar = React.forwardRef((props: HDSAvatarProperties, ref) => {
  const {
    disabled,
    borderless,
    size = 'medium',
    name,
    imageUrl,
    icon,
    iconStyle,
    iconCategory,
    className,
    ...rest
  } = props;
  return (
    <hds-avatar
      ref={ref}
      {...(disabled && {disabled})}
      {...(borderless && {borderless})}
      size={size}
      name={name}
      {...(className && {class: className})}
      {...(imageUrl && {'image-url': imageUrl})}
      {...(icon && {icon})}
      {...(iconStyle && {'icon-style': iconStyle})}
      {...(iconCategory && {'icon-category': iconCategory})}
      {...rest}
    ></hds-avatar>
  );
});

HDSAvatar.defaultProps = {
  disabled: false,
  borderless: false,
  size: 'medium',
  name: '',
  imageUrl: undefined,
  icon: undefined,
  iconCategory: 'core-ui',
  iconStyle: 'solid',
};

export default HDSAvatar;
